.MainPanelWrapper {
    width: auto;
    height: auto;
}

.MainPanelDiv {
    color: var(--text_primary);
    background: var(--modal_card_background);
    border-radius: 14px;
    box-shadow: 0 2px 24px 0 rgba(0,0,0,.08), 0 0 2px 0 rgba(0,0,0,.08);
    color: var(--dynamic_gray);
}

.FixedLayoutDiv {
    background: var(--modal_card_background);
    box-shadow: 0 2px 24px 0 rgba(0,0,0,.08), 0 0 2px 0 rgba(0,0,0,.08);
    border-radius: 14px 14px 0 0;
}